.container {
  margin: 0 auto;
  max-width: 800px;
  width: 80%;
}

main,
.nav-container {
  display: block;
  margin: 0 auto;
  max-width: 800px;
  width: 80%;
}

/*navi icon size*/
#head_icon{

  margin :  0;
  width : 45px;
}

.nav {
  box-shadow: 0 2px 2px -2px $shadow-color;
  overflow: auto;

  &-container {
    margin: 1rem auto;
    position: relative;
    text-align: center;
  }

  &-title {
    
    color: black;
    display: inline-block;
    margin: 0;
    padding-right: .2rem;

  }

  ul {
    list-style-type: none;
    margin: 1rem 0 0;
    padding: 0;
    text-align: center;
  }

  li {
    @include transition(all .2s ease-out);
    color: $default-color;
    display: inline-block;
    opacity: .6;
    padding: 0 2rem 0 0;

    &:last-child {
      padding-right: 0;
    }

    &:hover,
    &:focus {
      opacity: 1;
    }
  }

  a {
    color: $default-color;
    font-family: $sans-serif;
  }
}

@media (min-width: 600px) {
  .nav {
    &-container {
      text-align: left;
    }

    ul {
      bottom: 0;
      position: absolute;
      right: 0;
    }
  }
}

.footer_title{

  font-size: 12px;
 
}

.footer_email{

  font-size: 12px;
  line-height: 2.0;
}

footer {
  display: block;
  padding: 45px 0;
  
  background-color: #495057;
  color : #fff;
  padding: 2rem 0;
 
  .wrapper{
    max-width: 800px;
    margin-left:  auto;
    margin-right: auto ;
    width : 80%;
 
  }

  span {
    line-height: 3.0;
    font-size: .8rem;
  }
}